<template>
  <div class="form">
    <h1>Se connecter</h1>
    <p v-show="show" class="erreur">Erreur lors de la connexion : les identifiants sont inccorrects.</p>
    <div class="input-group">
      <input type="text" placeholder="username/email" v-model="username" />
    </div>
    <div class="input-group">
      <input type="password" placeholder="password" v-model="password" />
    </div>
    <button class="submitButton" @click="Login()">Se connecter</button>
    <p>
      Pas encore membre?
      <router-link to="/register">Inscrivez vous</router-link>
    </p>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      activeClass: "active",
      username: "",
      password: "",
      show: false,
    };
  },
  methods: {
    async Login() {
      const username = this.username;
      const password = this.password;
      try {
        await this.$store.dispatch("login/getToken", { username: username, password: password });
        await this.$store.dispatch("login/getProfile", {
          token: this.$store.getters["login/token"],
        });
        if (this.$store.getters["login/token"]) {
          this.$router.push({ name: "home" });
        } else {
          this.show = true;
        }
        //this.$router.push({ name: "home" });
      } catch (error) {
        console.error(error);
      }
    },
  },
};
</script>

