import store from "@/store";

export const redirectHome = (to, from, next) => {
  if (store.getters["login/token"]) {
    next("/home");
  } else {
    next();
  }
};

export const requireAuth = (to, from, next) => {
  if (!store.getters["login/token"]) {
    next("/login");
  } else {
    next();
  }
};
