import axios from "axios";

const state = {
  games: [],
  gameCache: null,
};

const mutations = {
  setGameTests(state, { games }) {
    state.games = games;
  },
  setGameTestCache(state, { game }) {
    state.gameCache = game;
  },
};

const actions = {
  async getAllGames({ commit }) {
    try {
      const response = await axios.get(`/api/games`);
      const games = response.data.data;
      commit("setGameTests", { games });
    } catch (e) {
      console.error(e);
    }
  },
  async getPublicGames({ commit }) {
    try {
      const response = await axios.get(`/api/games/public`);
      const games = response.data.data;
      commit("setGameTests", { games });
    } catch (error) {
      console.error(error);
    }
  },
  async getAllGamesByCategory({ commit }, { category }) {
    try {
      const response = await axios.get(`/api/games/category/${category}`);
      const games = response.data.data;
      commit("setGameTests", { games });
    } catch (e) {
      console.error(e);
    }
  },
  async getGame({ state, commit }, { gameId }) {
    if (state.games.length > 0) {
      state.gameCache = state.games.find((x) => x.id == gameId);
    }

    try {
      const response = await axios.get(`/api/game/${gameId}`);
      const game = response.data.data;
      commit("setGameTestCache", { game });
    } catch (e) {
      console.error(e);
    }
  },
  async putGame({ state, commit }, { game }) {
    try {
      const response = await axios.put(`/api/game`, game);
    } catch (e) {
      console.error(e);
    }
  },
};

export default {
  namespaced: true,
  state,
  actions,
  mutations,
};
