<template>
  <div class="form">
    <h1>S'inscrire</h1>
    <p v-show="show" class="erreur">Erreur : toutes les donnes n'ont pas t renseignes</p>
    <div class="input-group">
      <label>Username :</label>
      <input type="text" v-model="payload.username" />
    </div>
    <div class="input-group">
      <label>Password :</label>
      <input type="password" v-model="payload.password" />
    </div>
    <div class="input-group">
      <label>Email :</label>
      <input type="text" v-model="payload.email" />
    </div>
    <div class="input-group">
      <label>Nom :</label>
      <input type="text" v-model="payload.lastname" />
    </div>
    <div class="input-group">
      <label>Prenom :</label>
      <input type="text" v-model="payload.firstname" />
    </div>
    <div class="input-group">
      <label>Date :</label>
      <input type="date" v-model="payload.birthdate" />
    </div>
    <button class="submitButton" @click="register()">Confirmer</button>
  </div>
</template>

<script>
export default {
  name: "Register",
  data() {
    return {
      payload: {
        username: "",
        password: "",
        email: "",
        firstname: "",
        lastname: "",
        birthdate: "",
      },
      show: false,
    };
  },
  methods: {
    async register() {
      if (Object.values(this.payload).some((element) => element != "")) {
        await this.$store.dispatch("login/Register", this.payload);
        await this.$store.dispatch("login/getToken", {
          username: this.payload.username,
          password: this.payload.password,
        });
        await this.$store.dispatch("login/getProfile", {
          token: this.$store.getters["login/token"],
        });
        if (this.$store.getters["login/token"]) {
          this.$router.push({ name: "home" });
        }
      } else {
        this.show = true;
      }
    },
  },
};
</script>

