<template>
  <div class="header-menu">
    <div class="links">
      <router-link :to="{ name: 'home' }">HOME</router-link>
      <router-link :to="{ name: 'profile' }">MON PROFIL</router-link>
      <template v-if="IsLoggedIn">
        <router-link :to="{ name: 'login' }">LOGIN</router-link>
        <router-link :to="{ name: 'register' }">S'INSCRIRE</router-link>
      </template>
      <a @click="logout()" v-else>LOGOUT</a>
      <router-link :to="{ name: 'newTest' }" v-show="checkStatus">NOUVEAU TEST</router-link>
    </div>
    <!--div class="user">US</div-->
  </div>
</template>

<script>
export default {
  name: "HeaderMenu",
  data() {
    return {};
  },
  computed: {
    IsLoggedIn: function () {
      if (!this.$store.getters["login/token"]) {
        return true;
      }
      return false;
    },
    checkStatus: function () {
      if (this.$store.getters["login/profile"]) {
        const profile = this.$store.getters["login/profile"];
        if (profile.statut == "admin") {
          return true;
        }
      }
      return false;
    },
  },
  methods: {
    logout() {
      this.$store.dispatch("login/Logout", {}, { root: true });
      this.$router.push({ name: "acceuil" });
    },
  },
};
</script>

