import Vue from "vue";
import VueRouter from "vue-router";

import Home from "@/pages/home/Index.vue";
import NewTest from "@/pages/newTest/Index.vue";
import Category from "@/pages/category/Index.vue";
import News from "@/pages/news/Index.vue";
import Login from "@/pages/login/Index.vue";
import Register from "@/pages/register/Index.vue";
import Acceuil from "@/pages/acceuil/Index.vue";
import Profile from "@/pages/profile/Index.vue";

import { redirectHome, requireAuth } from "@/utils/auth";

Vue.use(VueRouter);

const routes = [
  {
    path: "/home",
    name: "home",
    component: Home,
  },
  {
    path: "/",
    name: "acceuil",
    beforeEnter: redirectHome,
    component: Acceuil,
  },
  {
    path: "/game/new",
    name: "newTest",
    beforeEnter: requireAuth,
    component: NewTest,
  },
  {
    path: "/category/:category",
    name: "category",
    component: Category,
  },
  {
    path: "/news/:id",
    name: "news",
    component: News,
  },
  {
    path: "/login",
    name: "login",
    beforeEnter: redirectHome,
    component: Login,
  },
  {
    path: "/register",
    name: "register",
    component: Register,
  },
  {
    path: "/profile/me",
    name: "profile",
    component: Profile,
    beforeEnter: requireAuth,
  },
];

const router = new VueRouter({
  routes,
  mode: "history",
});

export default router;
